.nav {
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.search-input-group {
  width: 100%;
}

.nation-div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}

.nation-flag {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.2rem;
}

.flag {
  height: 100%;
  box-shadow: rgba(14, 30, 37, 0.32) 0px 2px 4px 0px, rgba(14, 30, 37, 0.32) 0px 2px 16px 0px;
}

/* CSS */
.emotion {
  width: 2.5rem;
  height: 2.5rem;
  background-color: royalblue;
  border: 0;
  border-radius: 50%;
  color: #fff;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: system-ui,-apple-system,system-ui,"Segoe UI",Roboto,Ubuntu,"Helvetica Neue",sans-serif;
  font-size: 1rem;
  font-weight: 600;
  outline: 0;
  padding: 16px 21px;
  position: relative;
  text-align: center;
  text-decoration: none;
  transition: all .3s;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}

.emotion:before {
  background-color: initial;
  background-image: linear-gradient(#fff 0, rgba(255, 255, 255, 0) 100%);
  border-radius: 125px;
  content: "";
  height: 50%;
  left: 4%;
  opacity: .5;
  position: absolute;
  top: 0;
  transition: all .3s;
  width: 92%;
}

.emotion:hover {
  box-shadow: rgba(255, 255, 255, .2) 0 3px 15px inset, rgba(0, 0, 0, .1) 0 3px 5px, rgba(0, 0, 0, .1) 0 10px 13px;
}

@media (min-width: 768px) {
  .emotion {
    padding: 16px 48px;
  }
}